@import 'vars';
@import 'mixins';

/* Navaber */
.navbar {
    background: $white-color !important;
    padding: 15px;

    .nav-item {
        a {
            text-transform: uppercase;
            margin-right: 10px;
        }
        .btn {
            background-color: $green-color;
            color: $white-color;
            border-radius: 0px;
            text-transform: uppercase;
        }
    }

}

/* Carousel Slider */
.carousel-item {
    z-index: -1;

    .carousel-caption {
        top: 65%;
        -ms-transform: translateY(-65%);
        transform: translateY(-65%);

        h1 {
            text-transform: uppercase;
            font-weight: 500;
        }

    }

}

.banner-box {
    border: 4px solid $white-color;
    background-color: $green-color;
    width: 15%;
    height: 85px;
    margin-top: -42.5px;
    text-align: center;
    margin-left: 42%;
    padding: 10px;

    p {
        text-transform: uppercase;
        color: $white-color;
        font-weight: 600;
        margin-bottom: 0;
        font-size: 11px;
    }

    hr {
        margin: 10px 0 10px 0;
        background-color: $white-color;
        width: 15%;
        margin-left: 50%;
        transform: translateX(-50%);
    }
}

.carousel-item {
    z-index: -1;

    .carousel-caption {
        top: 65%;
        -ms-transform: translateY(-65%);
        transform: translateY(-65%);

        h1 {
            text-transform: uppercase;
            font-weight: 500;
        }

    }

}

/* First section */

.first-header {
    text-align: center;
    margin-top: 80px;
    h4 {
        text-transform: uppercase;
    }
    hr {
        margin: 10px 0 10px 0;
        background-color: $main-color;
        width: 5%;
        margin-left: 50%;
        transform: translateX(-50%);
    }
    p {
        color: $grey-color;
    }
}
.first-body {
    img {
        z-index: -1;
    }
    h1 {
        margin-top: -22px;
        color: $green-color;
        font-size: 55px;
    }
    p {
        color: $grey-color;
    }
    h3 {
        text-transform: uppercase;
    }
  .dash {
      color: $green-color;
      margin-top: -15px;
  }
}
.vl {
    border-left: 1px solid $green-color;
    height: 150px;
    margin-left: -3px;
    margin-top: 30px;
    margin-bottom: 30px;
    margin-left: 50%;
}

/* Testimonial section */
.testimonial {
    width: 100%;
    height: 900px;
    background-image: url(../images/testimonial/background.png);
    
    h4 {
        padding-top: 20px;
        text-align: center;
        text-transform: uppercase;
    }
    hr {
        margin: 10px 0 10px 0;
        background-color: $main-color;
        width: 3%;
        margin-left: 50%;
        transform: translateX(-50%);

    }
    .carousel {
        margin-left: 50%;
        transform: translateX(-50%);
        width: 70%;
        z-index: 1;
        .carousel-inner {
            p {
                margin-bottom: -4px;
            }
        }
        
        .carousel-control-next-icon:after
        {
          content: url(../images/icons/arrowright.png);
        }
        
        .carousel-control-prev-icon:after {
          content: url(../images/icons/arrowleft.png);
        }
    }
}
